<script>
    export default {
        name: 'TemplateComponent',
        data() {
            return {
                BaseUrl:process.env.BASE_URL||"",
                titulo: 'Componente Separado 2',
                mensagem: 'Este é um componente Vue com script separado.',
                Projetos: {},
                Paginacao: [1,0],
                Habilidades:{
                    'FRONT-END':{'Html':.7,'Css':.6,'JavaScript':.6},
                    'BACK-END':{'Node':.6,'Python':.5},
                    'FRAMEWORK':{'Express':.6,'Angular':.5,'Next':.6,'React':.7},
                    'DATABASE':{'Sql':.5,'MongoDB':.5},
                    'VERSIONAMENTO':{'Git':.5},
                    'MOBILE':{'ReactNative':.5},
                    'DESENVOLVIM...':{'VsCode':.7},
                    'DESIGN':{'Figma':.5},
                    'OUTROS':{'TypeScript':.6},
                },
                MenuNavAberto:false,
                TamanhoTela:window.innerWidth,
            };
        },
        methods: {
            projetosPaginados() {
                const inicio = this.Paginacao[0] * 9
                const fim = inicio + 9
                return Object.entries(this.Projetos).slice(inicio, fim).map(([projeto]) => projeto);
            },
            GerarPaginacao(){
                const botoes = [];
                const [paginaAtual, totalPaginas] = this.Paginacao;

                if (totalPaginas < 5) {
                    for (let i = 0; i <= totalPaginas; i++) {botoes.push(i);}
                } else {
                    botoes.push(0);
                    const de = paginaAtual - 2 < 1 ? paginaAtual - 1 < 1 ? paginaAtual : paginaAtual - 1 : paginaAtual - 2;
                    const ate = paginaAtual + 3 > totalPaginas ? totalPaginas : paginaAtual + 3;

                    if (de > 2) {botoes.push(null);}

                    for (let i = de; i <= ate - 1; i++) {
                        if (i !== 0 && i !== totalPaginas - 1) {
                            botoes.push(i);
                        }
                    }
                    if (ate < totalPaginas - 2) {botoes.push(null);}

                    botoes.push(totalPaginas - 1); 
                }
                return botoes;
            },
            SetPaginacao(NovaPaginacao){this.Paginacao=NovaPaginacao},
            async ObterDados(){
                try{
                    await fetch(`${this.BaseUrl}Informacoes.json`,{method:"GET"})
                    .then((Response)=>{
                        return Response.json()
                    }).then((data)=>{
                        this.Projetos=data
                        const totalPaginas = Math.floor(Object.keys(data).length / 9);
                        this.Paginacao=[0,totalPaginas]
                    })
                }catch(e){
                    console.log("Catch",e);
                }
            },
            SetMenuNavAberto(ValorSet){this.MenuNavAberto=ValorSet},
            TelaMaiorQue(Tamanho){return this.TamanhoTela>Tamanho},
            AtualizarTamanhoDaTela(){this.TamanhoTela=window.innerWidth}
        },
        mounted(){
          this.ObterDados()
          window.addEventListener('resize', this.AtualizarTamanhoDaTela);
        },
        before(){window.removeEventListener('resize', this.AtualizarTamanhoDaTela)}
    };
</script>
  
