<template>
     <nav class="BarraNavegacao">
        <RouterLink :to="'/'"><img src='Imagens/LogoNicolasASilva.png' alt="" /></RouterLink>
        <button v-if="!MenuNavAberto" @click="SetMenuNavAberto(true)"><img src="Imagens/Menu.svg" alt=""></button>
        <section v-if="MenuNavAberto || TelaMaiorQue(850)">
            <button @click="SetMenuNavAberto(false)"><img src="Imagens/Remover.svg" alt=""></button>
            <a href="#ArticleSobre" @click="SetMenuNavAberto(false)">Sobre o Projeto</a>
            <a href="#ArticleHabilidades" @click="SetMenuNavAberto(false)">Habilidades</a>
            <a href="#ArticleImagens" @click="SetMenuNavAberto(false)">Imagens</a>
        </section>
    </nav>
    <section class="ConteudoPortifolio">
        <h4>SOBRE O <span>PROJETO</span> </h4>
        <article id="ArticleSobre" class="ArticleSobre_detalhe">
            <div class="InfosPrincipal">
                <h5>NOME: <span>{{ Projeto ? Projeto.Nome : '' }}</span></h5>
                <h5>CODIGO FONTE: <a :href="Projeto ? Projeto.Codigo_Fonte || '' : ''">CODIGO NO GIT</a></h5>
                <h5>RESPONSIVO: <span>{{ Projeto ? Projeto.Responsivo : '' }}</span></h5>
                <h5>TIPO: <span>{{ Projeto ? Projeto.Tipo : '' }}</span></h5>
                <h5>COLABORADORES: <span>{{ Projeto ? Projeto.Colaborador : '' }}</span></h5>
            </div>

            <div class="InfosAdicionais_detalhe">
                <div class="CardInfoAdicional">
                    <h2>{{Projeto?Projeto.Duracao[0]:""}}<span>{{Projeto?Projeto.Duracao[1]:""}}</span></h2>
                    <div/>
                    <h3>Tempo de Projeto</h3>
                </div>
            </div>
        <div class="Descricao">{{Projeto?Projeto.Descricao:"Projeto não encontrado, verifique se houve alterações na pesquisa"}}</div>
        </article>

        <h4>MINHAS <span>HABILIDADES</span> </h4>
        <article v-if="Projeto" id="ArticleHabilidades" class="ArticleHabilidades_detalhe">
            <div class="Habilidade_detalhe" :key=Categoria v-for="(Habilits, Categoria) in Projeto.Habilidades">
                <h4>{{Categoria}}</h4>
                <div class="ListaHabilidades">
                    <div :key=Habilidade v-for="(Habilidade) in Habilits">
                        <h5>{{Habilidade}}</h5>
                    </div>
                </div>
            </div>
        </article>

        <h4> <span>IMAGENS</span> </h4>
        <article  v-if="Projeto" id="ArticleImagens" class="ArticleImagens_detalhe">
            <img :src='`${this.BaseUrl}${Imagem}`' :key=Index v-for="(Imagem,Index) in Projeto.Imagens"/>
        </article>
    </section>
</template>
  
<script>
    export default {
        name: 'TemplateComponent',
        data(){
            return {
                BaseUrl:process.env.BASE_URL||'',
                Projeto:null,
                MenuNavAberto:false,
                TamanhoTela:window.innerWidth,
            }
        },
        methods:{
            async ObterDados(){
                try{
                    await fetch(`${this.BaseUrl}Informacoes.json`,{method:"GET"})
                    .then((Response)=>{
                        return Response.json()
                    }).then((data)=>{
                        this.Projeto=data[this.id]
                    })
                }catch(e){
                    console.error("Catch",e);
                }
            },
            SetMenuNavAberto(ValorSet){this.MenuNavAberto=ValorSet},
            TelaMaiorQue(Tamanho){return this.TamanhoTela>Tamanho},
            AtualizarTamanhoDaTela(){this.TamanhoTela=window.innerWidth}
        },
        props: {
            id: {
                type: String,
                required: true
            }
        },
        mounted(){
          this.ObterDados()
          window.addEventListener('resize', this.AtualizarTamanhoDaTela);
        },
        before(){window.removeEventListener('resize', this.AtualizarTamanhoDaTela)}
    };
</script>
  
<style>@import url("./Style.css")</style>
  